import { Component, Input, OnInit, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { filter } from 'rxjs/operators';

import { User } from 'models';
import { MenuItem } from './models';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnChanges {
  @Input() public user: User;
  @Input() public loading = true;

  @Output() login = new EventEmitter<void>();
  @Output() logout = new EventEmitter<void>();

  public sideMenuOpen = false;
  public menues: MenuItem[];

  constructor(private router: Router) {}

  ngOnChanges(): void {
    this.menues = this.initMenues();
  }

  ngOnInit() {
    this.router.events.pipe(filter(event => event instanceof NavigationStart)).subscribe(() => {
      this.sideMenuOpen = false;
    });
  }

  onLogin() {
    this.sideMenuOpen = false;
    this.login.emit();
  }

  onLogout() {
    this.sideMenuOpen = false;
    this.logout.emit();
  }

  trackByFn(index: number) {
    return index;
  }

  private initMenues(): MenuItem[] {
    const menues = [
      { route: 'home', title: ' ' },
      {
        title: '',
        subMenues: [
          // { route: 'meetings', title: '' },
          { route: 'map', title: ' ' },
          { route: 'tell', title: '  ' },
          { route: 'host', title: '  ' },
          // { route: 'participate', title: '  ' }
          { route: 'home', title: '  ' }
          //           
        ]
      },
      {
        title: '',
        subMenues: [
          { route: 'about', title: '' },
          { route: 'team', title: ' ' },
          { route: 'articles', title: ' ' }
          // { route: 'news', title: ' ' }
        ]
      },
      { route: 'agenda', title: ' ' },
      { route: 'questions', title: ' ' },
      { route: 'gallery', title: '' },
      { route: 'contact', title: ' ' }
    ];

    if (this.user?.isAdmin || this.user?.isVolunteer) {
      menues.push({
        title: '',
        subMenues: [
          { route: 'admin-statistics', title: '' },
          { route: 'admin-bereaveds', title: ' ' },
          { route: 'admin-users', title: ' ' }
        ]
      });
    }

    return menues;
  }
}
